import { defaultMineFieldProps, mineFieldsProps } from 'const';
import { useMineField } from 'hooks/use-mine-field';
import { createContext } from 'react';
import { MineFieldsProps } from 'view-models';

type MinesweeperContextValue = Readonly<
  ReturnType<typeof useMineField> & { mineFieldsProps: MineFieldsProps }
>;

export const MinesweeperContext = createContext<MinesweeperContextValue>({
  mineFieldsProps,
  mineField: [],
  mineFieldProps: defaultMineFieldProps,
  mineFieldStatus: {
    isSetup: false,
    isBusted: false,
    isSolved: false,
    hiddenMinesCount: 0,
  },
  /* eslint-disable @typescript-eslint/no-empty-function */
  setMineFieldProps: () => {},
  setupMineField: () => {},
  revealCell: () => {},
  toggleFlag: () => {},
  revealNeighbors: () => {},
  reset: () => {},
});

export const { Provider: MinesweeperProvider } = MinesweeperContext;
