import { reject } from 'lodash-es';
import { CellChecker } from 'utils/cell-checker';
import { MineFieldVM } from 'view-models';

export class MineFieldChecker {
  // eslint-disable-next-line no-useless-constructor
  constructor(private readonly mineField: MineFieldVM) {}

  isBusted() {
    return this.mineField.some((cell) => new CellChecker(cell).isBusted);
  }

  isSolved() {
    return !reject(
      this.mineField,
      (cell) => new CellChecker(cell).isMined
    ).some((cell) => new CellChecker(cell).isHidden);
  }
}
