import './CellFactory.scss';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import React, { memo } from 'react';
import { CellChecker } from 'utils/cell-checker';
import { joinClassNames } from 'utils/join-class-names';
import { CellEvent, CellVM } from 'view-models';

import BombIcon from '../BombIcon';

type CellFactoryProps = Readonly<{
  cell: CellVM;
  onClick?: (cell: CellVM) => void;
  onMouseDown?: (event: CellEvent, cell: CellVM) => void;
  onContextMenu?: (cell: CellVM) => void;
}>;

const CellFactory = (props: CellFactoryProps) => {
  const { cell, onClick, onMouseDown, onContextMenu } = props;

  const { value } = cell;

  const {
    isHidden,
    isEmpty,
    isFlagged,
    isMined,
    isIncorrect,
    isBusted,
  } = new CellChecker(cell);

  const classNamePrefix = 'Cell';

  const handleClick = () => {
    onClick?.(cell);
  };

  const handleContextMenu = () => {
    onContextMenu?.(cell);
  };

  const handleMouseDown = (event: CellEvent) => {
    onMouseDown?.(event, cell);
  };

  if (isHidden) {
    return (
      <button
        type="button"
        aria-label="Cell"
        className={classNamePrefix}
        onClick={handleClick}
        onContextMenu={handleContextMenu}
      />
    );
  }

  if (isFlagged) {
    return (
      <button
        type="button"
        className={classNamePrefix}
        onContextMenu={handleContextMenu}
      >
        <FontAwesomeIcon icon={['far', 'flag']} />
      </button>
    );
  }

  if (isEmpty) {
    return (
      <button
        type="button"
        aria-label="Cell"
        className={joinClassNames(classNamePrefix, `${classNamePrefix}__empty`)}
      />
    );
  }

  if (isMined) {
    return <BombIcon className={classNamePrefix} />;
  }

  if (isIncorrect) {
    return (
      <BombIcon
        className={joinClassNames(
          classNamePrefix,
          `${classNamePrefix}__incorrect`
        )}
      />
    );
  }

  if (isBusted) {
    return (
      <BombIcon
        className={joinClassNames(
          classNamePrefix,
          `${classNamePrefix}__busted`
        )}
      />
    );
  }

  return (
    <button
      type="button"
      className={joinClassNames(
        classNamePrefix,
        `${classNamePrefix}__${value}`
      )}
      onMouseDown={handleMouseDown}
    >
      {value}
    </button>
  );
};

export default memo(CellFactory);
