import { CellAppearance, CellValue } from 'const';
import { useDidUpdate } from 'hooks/use-did-update';
import produce, { Draft } from 'immer';
import { difference, range } from 'lodash-es';
import { useCallback, useMemo, useState } from 'react';
import { addId } from 'utils/add-id';
import { CellChecker } from 'utils/cell-checker';
import { MineFieldChecker } from 'utils/mine-field-checker';
import { NeighborsUtils } from 'utils/neighbors-utils';
import { CellVM, MineFieldPropsVM, MineFieldVM } from 'view-models';

type DraftFn = (mineField: Draft<MineFieldVM>) => void;

export const useMineField = (initialMineFieldProps: MineFieldPropsVM) => {
  const [mineFieldProps, setMineFieldProps] = useState(initialMineFieldProps);

  const { rowsCount, columnsCount, minesCount } = mineFieldProps;

  const length = rowsCount * columnsCount;

  const emptyMineField = useMemo(
    () =>
      Array.from({ length }, (item, index) =>
        addId({
          index,
          value: CellValue.Empty,
          appearance: CellAppearance.Hidden,
        })
      ),
    [length]
  );

  const initialMineFieldStatus = useMemo(
    () => ({
      isSetup: false,
      isBusted: false,
      isSolved: false,
      hiddenMinesCount: minesCount,
    }),
    [minesCount]
  );

  const neighborsUtils = useMemo(
    () => new NeighborsUtils(rowsCount, columnsCount),
    [rowsCount, columnsCount]
  );

  const [mineField, setMineField] = useState(emptyMineField);

  const [mineFieldStatus, setMineFieldStatus] = useState(
    initialMineFieldStatus
  );

  const getFloodFilledMineField = useCallback(
    (prevMineField: CellVM[], cell: CellVM, draftFn?: DraftFn) =>
      produce(prevMineField, (draft) => {
        draftFn?.(draft);

        /* eslint-disable no-param-reassign */
        draft[cell.index].appearance = CellAppearance.Visible;

        (function floodFill(target: CellVM) {
          if (neighborsUtils.canFloodFill(draft, target)) {
            neighborsUtils.getIndexes(target).forEach((index) => {
              const neighbor = draft[index];

              const { isMined, isHidden, isFlagged } = new CellChecker(
                neighbor
              );

              if (!isMined && isHidden && !isFlagged) {
                neighbor.appearance = CellAppearance.Visible;

                floodFill(neighbor);
              }
            });
          }
        })(cell);
      }),
    [neighborsUtils]
  );

  const getBustedMineField = useCallback(
    (prevMineField: CellVM[], draftFn: DraftFn) =>
      produce(prevMineField, (draft) => {
        draftFn(draft);

        draft.forEach((cell, index) => {
          const { isUnsolved, isSolvedWrong } = new CellChecker(cell);

          if (isUnsolved) {
            cell.appearance = CellAppearance.Visible;
          } else if (isSolvedWrong) {
            draft[index] = {
              ...cell,
              value: CellValue.Incorrect,
              appearance: CellAppearance.Visible,
            };
          }
        });
      }),
    []
  );

  const setupMineField = useCallback(
    (cell: CellVM) => {
      setMineField((prevMineField) =>
        getFloodFilledMineField(prevMineField, cell, (draft) => {
          const indexes = difference(range(length), [
            cell.index,
            ...neighborsUtils.getIndexes(cell),
          ]);

          const minesIndexes = new Set<number>();

          while (minesIndexes.size < minesCount) {
            minesIndexes.add(
              indexes[Math.floor(Math.random() * indexes.length)]
            );
          }

          minesIndexes.forEach((index) => {
            draft[index].value = CellValue.Mine;
          });

          draft.forEach((item) => {
            if (!new CellChecker(item).isMined) {
              item.value = neighborsUtils.countMined(draft, item);
            }
          });
        })
      );

      setMineFieldStatus((prevMineFieldStatus) => ({
        ...prevMineFieldStatus,
        isSetup: true,
      }));
    },
    [getFloodFilledMineField, length, minesCount, neighborsUtils]
  );

  const revealCell = useCallback(
    (cell: CellVM) => {
      setMineField((prevMineField) =>
        new CellChecker(cell).isMined
          ? getBustedMineField(prevMineField, (draft) => {
              draft[cell.index] = {
                ...cell,
                value: CellValue.Busted,
                appearance: CellAppearance.Visible,
              };
            })
          : getFloodFilledMineField(prevMineField, cell)
      );
    },
    [getBustedMineField, getFloodFilledMineField]
  );

  const toggleFlag = useCallback((cell: CellVM) => {
    const { isFlagged } = new CellChecker(cell);

    setMineField((prevMineField) =>
      produce(prevMineField, (draft) => {
        draft[cell.index].appearance = isFlagged
          ? CellAppearance.Hidden
          : CellAppearance.Flagged;
      })
    );

    setMineFieldStatus((prevMineFieldStatus) =>
      produce(prevMineFieldStatus, (draft) => {
        draft.hiddenMinesCount += isFlagged ? 1 : -1;
      })
    );
  }, []);

  const revealNeighbors = useCallback(
    (cell: CellVM) => {
      setMineField((prevMineField) => {
        if (neighborsUtils.canFloodFill(prevMineField, cell)) {
          return getFloodFilledMineField(prevMineField, cell);
        }

        if (neighborsUtils.canReveal(prevMineField, cell)) {
          return getBustedMineField(prevMineField, (draft) => {
            neighborsUtils.getIndexes(cell).forEach((index) => {
              const neighbor = draft[index];

              const { isUnsolved, isSolvedWrong } = new CellChecker(neighbor);

              if (isUnsolved) {
                neighbor.value = CellValue.Busted;
              } else if (isSolvedWrong) {
                neighbor.value = CellValue.Incorrect;
              }

              neighbor.appearance = CellAppearance.Visible;
            });
          });
        }

        return prevMineField;
      });
    },
    [getBustedMineField, getFloodFilledMineField, neighborsUtils]
  );

  const reset = useCallback(() => {
    setMineField(emptyMineField);

    setMineFieldStatus(initialMineFieldStatus);
  }, [emptyMineField, initialMineFieldStatus]);

  useDidUpdate(() => {
    reset();
  }, emptyMineField);

  useDidUpdate(() => {
    const mineFieldChecker = new MineFieldChecker(mineField);

    if (mineFieldChecker.isBusted()) {
      setMineFieldStatus((prevMineFieldStatus) => ({
        ...prevMineFieldStatus,
        isBusted: true,
      }));
    } else if (mineFieldChecker.isSolved()) {
      setMineField((prevMineField) =>
        produce(prevMineField, (draft) => {
          draft.forEach((cell) => {
            if (new CellChecker(cell).isMined) {
              cell.appearance = CellAppearance.Flagged;
            }
          });
        })
      );

      setMineFieldStatus((prevMineFieldStatus) => ({
        ...prevMineFieldStatus,
        isSolved: true,
        hiddenMinesCount: 0,
      }));
    }
  }, mineField);

  return {
    mineField,
    mineFieldProps,
    mineFieldStatus,
    setMineFieldProps,
    setupMineField,
    revealCell,
    toggleFlag,
    revealNeighbors,
    reset,
  };
};
