import { CellAppearance, CellValue } from 'const';
import { CellVM } from 'view-models';

export class CellChecker {
  // eslint-disable-next-line no-useless-constructor
  constructor(private readonly cell: CellVM) {}

  private checkCell(prop: keyof CellVM, criteria: CellValue | CellAppearance) {
    return this.cell[prop] === criteria;
  }

  get isEmpty() {
    return this.checkCell('value', CellValue.Empty);
  }

  get isMined() {
    return this.checkCell('value', CellValue.Mine);
  }

  get isIncorrect() {
    return this.checkCell('value', CellValue.Incorrect);
  }

  get isBusted() {
    return this.checkCell('value', CellValue.Busted);
  }

  get isHidden() {
    return this.checkCell('appearance', CellAppearance.Hidden);
  }

  get isFlagged() {
    return this.checkCell('appearance', CellAppearance.Flagged);
  }

  get isUnsolved() {
    return this.isMined && !this.isFlagged;
  }

  get isSolvedWrong() {
    return !this.isMined && this.isFlagged;
  }
}
