import { CellChecker } from 'utils/cell-checker';
import { IndexUtils } from 'utils/index-utils';
import { CellVM, MineFieldVM } from 'view-models';

const isValidArrayIndex = (index: number, length: number) =>
  index > -1 && index < length;

export class NeighborsUtils {
  // eslint-disable-next-line no-useless-constructor
  constructor(
    private readonly mineFieldRowsCount: number,
    private readonly mineFieldColumnsCount: number,
    private readonly indexUtils = new IndexUtils(mineFieldRowsCount)
  ) {}

  getIndexes({ index }: CellVM) {
    const indexes: number[] = [];

    const { rowIndex, columnIndex } = this.indexUtils.to2D(index);

    for (let rowIndexOffset = -1; rowIndexOffset < 2; rowIndexOffset += 1) {
      for (
        let columnIndexOffset = -1;
        columnIndexOffset < 2;
        columnIndexOffset += 1
      ) {
        if (rowIndexOffset || columnIndexOffset) {
          const targetedRowIndex = rowIndex + rowIndexOffset;
          const targetedColumnIndex = columnIndex + columnIndexOffset;

          if (
            isValidArrayIndex(targetedRowIndex, this.mineFieldRowsCount) &&
            isValidArrayIndex(targetedColumnIndex, this.mineFieldColumnsCount)
          ) {
            indexes.push(
              this.indexUtils.to1D(targetedRowIndex, targetedColumnIndex)
            );
          }
        }
      }
    }

    return indexes;
  }

  canFloodFill(mineField: MineFieldVM, cell: CellVM) {
    return !this.getIndexes(cell).some(
      (index) => new CellChecker(mineField[index]).isUnsolved
    );
  }

  canReveal(mineField: MineFieldVM, cell: CellVM) {
    return (
      this.countMined(mineField, cell) ===
      this.countBy(
        mineField,
        cell,
        (neighbor) => new CellChecker(neighbor).isFlagged
      )
    );
  }

  countMined(mineField: MineFieldVM, cell: CellVM) {
    return this.countBy(
      mineField,
      cell,
      (neighbor) => new CellChecker(neighbor).isMined
    );
  }

  private countBy(
    mineField: MineFieldVM,
    cell: CellVM,
    criteriaFn: (item: CellVM) => boolean
  ) {
    return this.getIndexes(cell).reduce(
      (acc, neighborIndex) =>
        criteriaFn(mineField[neighborIndex]) ? acc + 1 : acc,
      0
    );
  }
}
