import { addId } from 'utils/add-id';
import { MineFieldsProps } from 'view-models';

/* eslint-disable no-shadow */
export enum MineFieldRowsCount {
  SM = 9,
  MD = 16,
  BG = 30,
}

export enum MineFieldColumnsCount {
  SM = 9,
  MD = 16,
  BG = 16,
}

export enum MineFieldMinesCount {
  SM = 10,
  MD = 40,
  BG = 99,
}

export enum MineFieldPropsKey {
  SM,
  MD,
  BG,
}

export enum CellValue {
  Empty,
  One,
  Two,
  Three,
  Four,
  Five,
  Six,
  Seven,
  Eight,
  Mine,
  Incorrect,
  Busted,
}

export enum CellAppearance {
  Hidden,
  Visible,
  Flagged,
}

const smallMineFieldProps = addId({
  name: 'Beginner',
  rowsCount: MineFieldRowsCount.SM,
  columnsCount: MineFieldColumnsCount.SM,
  minesCount: MineFieldMinesCount.SM,
});

export const mineFieldsProps: MineFieldsProps = new Map()
  .set(MineFieldPropsKey.SM, smallMineFieldProps)
  .set(
    MineFieldPropsKey.MD,
    addId({
      name: 'Intermediate',
      rowsCount: MineFieldRowsCount.MD,
      columnsCount: MineFieldColumnsCount.MD,
      minesCount: MineFieldMinesCount.MD,
    })
  )
  .set(
    MineFieldPropsKey.BG,
    addId({
      name: 'Expert',
      rowsCount: MineFieldRowsCount.BG,
      columnsCount: MineFieldColumnsCount.BG,
      minesCount: MineFieldMinesCount.BG,
    })
  );

export { smallMineFieldProps as defaultMineFieldProps };
