import React, { memo } from 'react';

const formatCount = (count: number) => {
  if (count < 1) {
    return '000';
  }

  if (count < 10) {
    return `00${count}`;
  }

  if (count < 100) {
    return `0${count}`;
  }

  return count.toString();
};

const Counter = ({ count }: Readonly<{ count: number }>) => (
  <div className="Counter">{formatCount(count)}</div>
);

export default memo(Counter);
