import { useEffect, useRef } from 'react';

type Fn = () => void;

export const useInterval = (fn: Fn, delay?: number) => {
  const fnRef = useRef<Fn>();

  useEffect(() => {
    fnRef.current = fn;
  });

  // eslint-disable-next-line consistent-return
  useEffect(() => {
    if (delay) {
      const intervalId = setInterval(() => {
        fnRef.current?.();
      }, delay);

      return () => {
        clearInterval(intervalId);
      };
    }
  }, [delay]);
};
