export class IndexUtils {
  // eslint-disable-next-line no-useless-constructor
  constructor(private readonly mineFieldRowsCount: number) {}

  to2D(index: number) {
    const rowIndex = index % this.mineFieldRowsCount;

    return {
      rowIndex,
      columnIndex: (index - rowIndex) / this.mineFieldRowsCount,
    };
  }

  to1D(rowIndex: number, columnIndex: number) {
    return columnIndex * this.mineFieldRowsCount + rowIndex;
  }
}
