import './FaceIcon.scss';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import React, { memo } from 'react';

type FaceIconProps = Readonly<{ icon?: JSX.Element; onClick: () => void }>;

const FaceIcon = ({
  icon = <FontAwesomeIcon icon={['far', 'meh']} />,
  onClick,
}: FaceIconProps) => (
  <button type="button" className="FaceIcon" onClick={onClick}>
    {icon}
  </button>
);

export default memo(FaceIcon);
