import React, { memo } from 'react';

type RadioButtonProps<T> = Readonly<{
  item: T;
  name: string;
  checked?: boolean;
  className?: string;
  onChange: (item: T) => void;
}>;

const RadioButton = <T extends Readonly<{ name: string }>>({
  item,
  name,
  checked,
  className,
  onChange,
}: RadioButtonProps<T>) => {
  const handleChange = () => {
    onChange(item);
  };

  return (
    <>
      <input
        type="radio"
        name={name}
        className={className}
        defaultChecked={checked}
        onChange={handleChange}
      />
      {item.name}
    </>
  );
};

export default memo(RadioButton) as typeof RadioButton;
