import './Indicators.scss';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import Counter from 'components/Counter';
import FaceIcon from 'components/FaceIcon';
import { MinesweeperContext } from 'contexts/minesweeper';
import { useDidUpdate } from 'hooks/use-did-update';
import { useInterval } from 'hooks/use-interval';
import React, { useCallback, useContext, useState } from 'react';

const INTERVAL_DELAY = 1e3;

const Indicators = () => {
  const {
    mineFieldStatus: { isSetup, isSolved, isBusted, hiddenMinesCount },
    mineFieldProps,
    reset,
  } = useContext(MinesweeperContext);

  const [secondsCount, setSecondsCount] = useState(0);

  let faceIcon: JSX.Element | undefined;

  if (isSolved) {
    faceIcon = <FontAwesomeIcon icon={['far', 'smile']} />;
  } else if (isBusted) {
    faceIcon = <FontAwesomeIcon icon={['far', 'frown']} />;
  }

  const handleFaceIconClick = useCallback(() => {
    setSecondsCount(0);
    reset();
  }, [reset]);

  useInterval(
    () => {
      setSecondsCount(secondsCount + 1);
    },
    isSetup && !(isSolved || isBusted) ? INTERVAL_DELAY : void 0
  );

  useDidUpdate(() => {
    setSecondsCount(0);
  }, mineFieldProps);

  return (
    <div className="Indicators">
      <Counter count={hiddenMinesCount} />
      <FaceIcon icon={faceIcon} onClick={handleFaceIconClick} />
      <Counter count={secondsCount} />
    </div>
  );
};

export default Indicators;
